<template>
  <div class="force">
    <a href="https://www.google.com">Hi this is a test link</a>
  </div>
</template>

<script>
import * as d3 from "d3";

const width = 300;
const height = 300;
const nodes = [{}, {}, {}];

function ticked() {
  const u = d3
    .select("svg")
    .selectAll("circle")
    .data(nodes);

  u.enter()
    .append("circle")
    .attr("r", 5)
    .merge(u)
    .attr("cx", d => {
      return d.x;
    })
    .attr("cy", d => {
      return d.y;
    });

  u.exit().remove();
}

const simulation = d3
  .forceSimulation(nodes)
  .force("charge", d3.forceManyBody())
  .force("center", d3.forceCenter(width / 2, height / 2))
  .on("tick", ticked);

export default {
  name: "ForceDiagram",
  props: {}
};
</script>

